import axios from 'axios';


const state = {
    cateleftlistmap:[],
    caterightlistmap:[],
    CateDetails:[],
    cateDetailsfalg:true,
    CateNavList:[],
    CateNavIndex:0,
};

const mutations ={
    setcateleftlistmap:function(state,params){
        state.cateleftlistmap = params;
    },
    setcarerightlistmap:function(state,params){
        state.caterightlistmap = params;
    },
    setCateDetails:function(state,params){
        state.CateDetails = params;
    },
    setCateDetailsfalg:function(state,params){
        state.cateDetailsfalg = params;
    },
    setCateNavList:function(state,params){
        state.CateNavList = params;
    },
    setCateNavIndex:function(state,params){
        state.CateNavIndex = params;
    },
};

const actions = {
    //分类左侧数据接口
    requesleftlistmap(context,params){
        axios.get('/api/category/listmap',{
            baseURL:'https://www.littleemmayang.com',
        })
        .then(response=>{
            if(response.status == 200 && response.data.message=='ok'){
                let result = response.data.data.map(({id,name})=>({id,name}));
                context.commit('setcateleftlistmap',result);
            }
        })
        .catch(err=>console.log(err));
    },
    //分类右侧数据
    requesrightlistmap(context,params){
        if(!params){
            params = 'categoryId=11';
        }else{
            params = 'categoryId=' + params;
        }
        axios.get('/api/category/listmap/group?' + params,{
            baseURL:'https://www.littleemmayang.com',
        })
        .then(response=>{
            if(response.status == 200 && response.data.message=='ok'){
                //处理需要的数据
                 let result =response.data.data.categoryGroupList.map(ele=>{
                    let obj = {
                        id :ele.id,
                        name : ele.name,
                    }
                    let arr =  ele.categoryList.map(({id,name,wapBannerUrl})=>({id,name,wapBannerUrl}));
                    arr.unshift(obj);
                    return arr;
                 })
                //处理轮播图数据
                let bannerlist = response.data.data.currentCategory.bannerList.map(({picUrl,id})=>({picUrl,id}));
                //将轮播图数据与内容数据合并
                result.unshift(bannerlist,response.data.data.currentCategory.id);
                context.commit('setcarerightlistmap',result);
            }
        })
        .catch(err=>console.log(err));
    },
    //分类详情页数据
    requesCateDetails (context,params){
        axios.get(params,{
            baseURL:'https://www.littleemmayang.com',
        })
        .then(response=>{
            if(response.status == 200 && response.data.message=='ok'){
                //处理需要的数据
                let { frontName,id } = response.data.data.category;
                let title = {
                    frontName,
                    id
                };
                let result =response.data.data.itemList.map(({id,name,simpleDesc,counterPrice,retailPrice,itemTagList,listPicUrl,listPromBanner})=>({id,name,simpleDesc,counterPrice,retailPrice,itemTagList,listPicUrl,listPromBanner}));
                result.map(ele=>{
                    //处理未优惠的价格
                    if(ele.counterPrice == ele.retailPrice){
                        ele.counterPrice = null;
                    } 
                    ele.itemTagList = ele.itemTagList.map(({itemId,name})=>({itemId,name}));
                    ele.listPromBanner =  ele.listPromBanner &&  ele.listPromBanner.usualContent.map(({value})=>({value}));
                });
                result.unshift(title);
                context.commit('setCateDetails',result);
                
            };     
        });
    },
    //分类详情页导航
    requesCateDetailsNav(context,params){
        axios.get(params,{
            baseURL:'https://www.littleemmayang.com',
        })
        .then(response=>{
            if(response.status == 200 && response.data.message=='ok'){
               let result = response.data.data.categoryGroupList[0].categoryList.map(({id,name})=>({id,name}));
               context.commit('setCateNavList',result);
            }
           
        })
    }


    
};

export default {
    namespaced:true,
    state,
    mutations,
    actions
}