import axios from 'axios';

const state = {
    topicNavlist:[],
    topicInformation:[],
};

const mutations = {
    setTopicNavlist:function(state,params){
        state.topicNavlist = params;
    },
    setTopicInformation:function(state,params){
        state.topicInformation = params;
    },
};


const actions = {
    //请求识物导航数据
    requesTopicNavlist(context,params){
        axios.get('/api/topic/find/getTabs')
        .then(response=>{
            if(response.status == 200 && response.data.message=='ok' ){
                let result = response.data.data.map(({tabId,tabName}) => ({tabId,tabName}));
                context.commit('setTopicNavlist',result);
            }
        })
        .catch(err=>{
            console.log(err);
        })
    },
    //请求具体信息数据
    requesTopicInformation(context,params){
        axios.get('/api/topic/find/getTabShowBanner')
        .then(response=>{
            if(response.status == 200 && response.data.message=='ok' ){
                let result = response.data.data.lookList.map((ele) => {
                    return ele.banner.picUrl;
                });
                context.commit('setTopicInformation',result);
            }
        })
        .catch(err=>{
            console.log(err);
        })
    },
};

export default {
    namespaced:true,
    state,
    mutations,
    actions
}