export default [
    {
        path:'/',
        component:()=>import('../pages/Home/home'),
        children:[
            {
                path:'homeNav/:id',
                component:()=>import('../pages/Home/home/childre/navigation'),
            }
        ]
        
    },
    {
        path:'/home/list',
        component:()=>import('../pages/Home/list'),
    },
    {
        path:'/home/details',
        component:()=>import('../pages/Home/details'),
    },
]