import axios from 'axios';

const state = {
    homeNavIndex:0,
    homeNavList:[],
    bannerList:[],
    policyDescList:[],
    kingKongList:[],
    homeTagList:[],
    categoryList:[],
    popularItemList:[],
    itemList:[],
    newItemList:[],
    sceneLightShoppingGuideModule:[],
    categoryModule:[],
    homeNavIgaTion:[],
}


const mutations={
    sethomeNavIndex(state,data){
        state.homeNavIndex = data;
    },
    sethomeNavList(state,data){
        state.homeNavList = data;
    },
    sethomeBanner(state,data){
        state.bannerList = data;
    },
    setpolicyDescList(state,data){
        state.policyDescList = data;
    },
    setkingKongList(state,data){
        state.kingKongList = data;
    },
    sethomeTag(state,data){
        state.homeTagList = data;
    },
    setcategoryList(state,data){
        state.categoryList = data;
    },
    setpopularItemList(state,data){
        state.popularItemList = data;
    },
    setitemList(state,data){
        state.itemList = data;
    },
    setnewItemList(state,data){
        state.newItemList = data;
    },
    setsceneLightShoppingGuideModule(state,data){
        state.sceneLightShoppingGuideModule = data;
    },
    setcategoryModule(state,data){
        state.categoryModule = data;
    },
    sethomeNavIgaTion(state,data){
        state.homeNavIgaTion = data;
    },
}

const actions={
    //首页导航栏
    requesHomeNavList(context, params){
        axios.get(params)
        .then((response) => {
            if(response.data.code == 0 && response.status == 200){
                let result = response.data.data.map(({id,name})=>({id,name}));
                result.unshift({id:-1,name:'推荐'});
                context.commit('sethomeNavList',result);
            }else{
                console.log('请求失败')
            }
        }).catch(err=>{
            console.log(err)
        })
    },
    //首页banner图
    requesBannerList(context, params){
        axios.get(params)
        .then((response) => {
            if(response.data.code == 0 && response.status == 200){
                let result = response.data.data.map(({id,picUrl})=>({id,picUrl}));
                context.commit('sethomeBanner',result);
            }else{
                console.log('请求失败')
            }
        }).catch(err=>{
            console.log(err)
        })
    },
    //网易自营品牌
    requesPolicyDescList(context, params){
        axios.get(params)
        .then((response) => {
            if(response.data.code == 0 && response.status == 200){            
                let result = response.data.data.map(({desc,icon})=>({desc,icon}));
                context.commit('setpolicyDescList',result);
            }else{
                console.log('请求失败');
            }
        }).catch(err=>{
            console.log(err);
        })
    },
    //首页新品首发
    requesKingKongList(context, params){
        axios.get(params)
        .then((response) => {
            if(response.data.code == 0 && response.status == 200){       
                let result = response.data.data.map(({sequen,picUrl,text,schemeUrl})=>({sequen,picUrl,text,schemeUrl}));
                context.commit('setkingKongList',result);
            }else{
                console.log('请求失败');
            }
        }).catch(err=>{
            console.log(err);
        })
    },
    //首页海外商品
    requesHomeTag(context, params){
        axios.get(params)
        .then((response) => {
            if(response.data.code == 0 && response.status == 200){    
                let result = response.data.data.map(({id,picUrl,name,floorPrice})=>({id,picUrl,name,floorPrice}));
                context.commit('sethomeTag',result);
            }else{
                console.log('请求失败')
            }
        }).catch(err=>{
            console.log(err)
        })
    },
    //首页类目热销榜
    requesCategoryList(context, params){
        axios.get(params)
        .then((response) => {
            if(response.data.code == 0 && response.status == 200){    
                let result = response.data.data.map(({categoryName,picUrl})=>({categoryName,picUrl}));
                context.commit('setcategoryList',result);
            }else{
                console.log('请求失败')
            }
        }).catch(err=>{
            console.log(err)
        })
    },
    //首页人气推荐
    requesPopularItemList(context, params){
        axios.get(params)
        .then((response) => {
            if(response.data.code == 0 && response.status == 200){    
                let result = response.data.data.map(({name,primaryPicUrl,retailPrice,tagCnts,id})=>({name,primaryPicUrl,retailPrice,tagCnts,id}));
                context.commit('setpopularItemList',result);
            }else{
                console.log('请求失败')
            }
        }).catch(err=>{
            console.log(err)
        })
    },
    //首页限时购
    requesItemList(context, params){
        axios.get(params)
        .then((response) => {
            if(response.data.code == 0 && response.status == 200){   
                let result = response.data.data.map(({itemId,originPrice,activityPrice,picUrl,showPicUrl})=>({itemId,originPrice,activityPrice,picUrl,showPicUrl}));
                context.commit('setitemList',result);
            }else{
                console.log('请求失败')
            }
        }).catch(err=>{
            console.log(err)
        })
    },
     //请求新品首发列表
     requesNewItemList(context, params){
        axios.get(params)
        .then((response) => {
            if(response.data.code == 0 && response.status == 200){   
                let result = response.data.data.map(({id,name,retailPrice,primaryPicUrl})=>({id,name,retailPrice,primaryPicUrl}));
                context.commit('setnewItemList',result);
            }else{
                console.log('请求失败')
            }
        }).catch(err=>{
            console.log(err)
        })
    },
     //请求99超值专场
     requesSceneLightShoppingGuideModule(context, params){
        axios.get(params)
        .then((response) => {
            if(response.data.code == 0 && response.status == 200){   
                let results = response.data.data.map((ele)=>{
                   return ele.styleItem;
                });
                let result = results.map(({desc,title,picUrlList})=>({desc,title,picUrlList}));
                context.commit('setsceneLightShoppingGuideModule',result);
            }else{
                console.log('请求失败')
            }
        }).catch(err=>{
            console.log(err)
        })
    },
    //底部展示分类
    requesCategoryModule(context, params){
        axios.get(params)
        .then((response) => {
            if(response.data.code == 0 && response.status == 200){
                let result = response.data.data.map(({itemList,titlePicUrl})=>({itemList,titlePicUrl}));
                let results = result.map((ele)=>{
                    let arr =  ele.itemList.map(({counterPrice,id,name,primaryPicUrl,retailPrice,tagCnts,colorNum})=>({counterPrice,id,name,primaryPicUrl,retailPrice,tagCnts,colorNum}))
                    arr.map(ele=>{
                        if(ele.retailPrice == ele.counterPrice){
                            ele.counterPrice=null;
                        }
                    })
                    var ti = ele.titlePicUrl;
                    arr.unshift(ti);
                   return arr;
                });
                context.commit('setcategoryModule',results);
            }else{
                console.log('请求失败')
            }
        }).catch(err=>{
            console.log(err)
        })
    },
    //首页导航页面数据请求
    requesHomeNavIgaTion(context, params){
        let url = params[0] + '?id=' + params[1];
        axios.get(url)
        .then((response) => {
            if(response.data.code == 0 && response.status == 200){
                //将轮播图与数据字段分别提取处理
                let data =response.data.data.slice(1);
                //处理轮播图数据
                let bannerLists = response.data.data.shift(0).BannerList.map(({picUrl})=>({picUrl}));
                //处理数据
                let result = data.map(ele=>{
                    //获取category对象中需要的字段数据
                    let {frontName,id,name,wapBannerUrl}= ele.category;
                     //将数据保存为新的category对象
                    let category = {frontName,id,name, wapBannerUrl }
                    //获取itemList对象中需要的字段数据，返回一个数组保存
                    let arr = ele.itemList.map(({id,name,tagCnts,simpleDesc,counterPrice,retailPrice,listPicUrl,colorNum})=>
                        ({id,name,tagCnts,simpleDesc,counterPrice,retailPrice,listPicUrl,colorNum}));
                    //遍历数组,处理原价与优惠价字段，相同价格，则原价取为null
                    arr.map(ele=>{
                        if(ele.counterPrice == ele.retailPrice){
                            ele.retailPrice = null;
                        }
                    })
                    //将新category并入数组,以便数据传输
                    arr.unshift(category);
                    //将最终处理完毕的数据返回
                    return arr;
                });
                 //将新bannerLists并入数组,以便数据传输
                result.unshift(bannerLists);
                context.commit('sethomeNavIgaTion',result);
            }else{
                console.log('请求失败')
            }
        }).catch(err=>{
            console.log(err)
        })
    },
}


export default {
    namespaced:true,
    state,
    mutations,
    actions
}